import React from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { getStorage, ref, uploadBytes, getDownloadURL } from 'firebase/storage';
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import Table from 'react-bootstrap/Table';
import Form from 'react-bootstrap/Form';
import { Row, Col } from 'react-bootstrap';
import '../css/rewardAdmin.css';

export default class viewReward extends React.Component {
    state = {
        data: [],
        rewardName: '',
        rewardPoints: '',
        url: ''
    }

    componentDidMount() {
        axios.get('http://localhost:8081/api/rewards')
            .then(res => {
                this.setState({ data: res.data });
            })
    }

    handleDelete = event => {
        const id = event.target.id;
        axios.delete('http://localhost:8081/api/rewards/' + id)
            .then(res => {
                window.alert("Reward deleted successfully");
                window.location.reload();
            })
    }

    handleName = event => {
        this.setState({ rewardName: event.target.value });
    }

    handlePoints = event => {
        this.setState({ rewardPoints: event.target.value });
    }

    handleURL = event => {
        this.setState({ url: event.target.files[0], });
    }

    handleSubmit = event => {
        event.preventDefault();
        // file upload
        // packing data    
        const storage = getStorage();
        const storageRef = ref(storage, 'img/' + this.state.url.name);
        var file = this.state.url;
        // Create file metadata including the content type
        /** @type {any} */
        const metadata = {
            contentType: this.state.url.type,
        };
        uploadBytes(storageRef, file, metadata);

        getDownloadURL(storageRef).then((downloadURL) => {
            console.log('File available at', downloadURL);

            const reward = {
                rewardName: this.state.rewardName,
                ptsRequired: this.state.rewardPoints,
                url: downloadURL
            };
            console.log("reward" + JSON.stringify(reward))

            const config = {
                headers: {
                    'content-type': 'application/json'
                }
            }

            //axios.post('https://ades-ca1-heroku.herokuapp.com/api/rewards', reward, config )
            axios.post('http://localhost:8081/api/rewards', reward, config)
                .then(res => {
                    console.log(res);
                    console.log(res.data);
                    window.location.reload();
                })
        })
    }

    render() {
        const data = this.state.data;
        return (
            <div className="rewardAdmin">
                <h1>Reward Administration</h1>
                <Accordion>
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>
                            <Button variant="outline-info" >Add Reward</Button>
                        </Accordion.Header>
                        <Accordion.Body>
                        <div className="form">
                            <Form onSubmit={this.handleSubmit} className="form">
                                <Row>
                                    <Col xs={12} md={8} lg={4}>
                                        <Form.Control placeholder="Reward Name" onChange={this.handleName}/>
                                    </Col>
                                    <Col xs={12} md={5} lg={3}>
                                        <Form.Control type="number" placeholder="Points Required" onChange={this.handlePoints} />
                                    </Col>
                                    <Col xs={12} md={5} lg={3}>
                                        <Form.Control type="file" onChange={this.handleURL}/>
                                    </Col>
                                    <Col xs={12} md={2} lg={2}>
                                    <Button variant="primary" type="submit">Add</Button>
                                    </Col>
                                </Row>
                            </Form>
                        </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <div id="viewRewards">
                    <Table responsive="sm" className="table">
                        <thead>
                        <tr>
                            <th>Name</th>
                            <th>Points</th>
                            <th>Picture</th>
                            <th>Edit?</th>
                            <th>Delete?</th>
                        </tr>
                        </thead>
                        <tbody>
                        {data && data.map(item =>
                            <tr key={item.rewardID}>
                                <td>{item.rewardName}</td>
                                <td>{item.ptsRequired}</td>
                                <td><img src={item.url} style={{height:180,width:180}}></img></td>
                                <td><Button type="button" variant="warning">Edit</Button></td>
                                <td><Button type="button" variant="danger" id={item.rewardID} onClick={this.handleDelete}>Delete</Button></td>
                            </tr>
                        )}
                        </tbody>
                    </Table>
                </div>
            </div>
        )
    }
}